/* faded button */
@each $color,
$value in $theme-colors {
    .btn-#{$color}-faded {
        @include btn-variant-faded(rgba($value, $btn-faded-bg-opacity), $value);
    }
}

a {
    text-decoration: none;
    color: inherit;
  }
  .cta {
    position: relative;
    margin: auto;
    padding: 16px 22px;
    transition: all .2s ease;
    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      border-radius: 28px;
      background: rgba($yellow,.5);
      width: 56px;
      height: 56px;
      transition: all .3s ease;
    }
    span {
      position: relative;
      font-size: 16px;
      line-height: 18px;
      font-weight: 700;
      letter-spacing: .25em;
      text-transform: uppercase;
      vertical-align: middle;
    }
    svg {
      position: relative;
      top: 0;
      margin-left: 10px;
      fill: none;
      stroke-linecap: round;
      stroke-linejoin: round;
      stroke: $yellow;
      stroke-width: 2;
      transform: translateX(-5px);
      transition: all .3s ease;
    }
    &:hover {
      &:before {
        width: 100%;
        background: rgba($primary,1);
      }
      svg {
        transform: translateX(0);
      }
      
    }
    &:active {
      transform: scale(.96);
    }
    
    
  }






.link {
  color: $primary;
    cursor: pointer;
    font-weight: 400;
  text-decoration: none;
}

.link--arrowed {
  display: inline-block;
    height: 2rem;
    line-height: 2rem;
  
  .arrow-icon {
    position: relative;
    top: -1px;
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    vertical-align: middle;
  }
  
  .arrow-icon--circle {
    transition: stroke-dashoffset .3s ease;
    stroke-dasharray: 95;
    stroke-dashoffset: 95;
  }
  
  &:hover {
    .arrow-icon {
      transform: translate3d(5px, 0, 0);
    }
    
    .arrow-icon--circle {
      stroke-dashoffset: 0;
    }
  }
}

a, a > span {
    position: relative;
    color: inherit;
    text-decoration: none;
    line-height: 24px;
    &:before, &:after {
      content: '';
      position: absolute;
      transition: transform .5s ease;
    }
  }

  
//------- {{ effect-1 }} -------//

.effect-1 {
    padding-top: 10px;
    &:before {
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      background: darken($warning, 10%);
      transform:  scaleX(0);
    }
    &:hover:before {
      transform:  scaleX(1);
    }
  }
  
  //------- {{ effect-2 }} -------//
  
  .effect-2 {
    padding: 10px;
    &:before, &:after {
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border-style: solid;
      border-color: darken($info, 10%);
    }
    &:before {
      border-width: 2px 0 2px 0;
      transform:  scaleX(0);
    }
    &:after {
      border-width: 0 2px 0 2px;
      transform:  scaleY(0);
    }
    &:hover:before, &:hover:after {
      transform:  scale(1, 1);
    }
  }
  
  //------- {{ effect-3 }} -------//
  
  .effect-3 {
    display: inline-block;
    padding-top: 10px;
    padding-bottom: 5px;
    overflow: hidden;
    &:before {
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      background: darken(#0BAAD4, 10%);
      transform:  translateX(-100%);
    }
    &:hover:before {
      transform:  translateX(0);
    }
  }
  
  //------- {{ effect-4 }} -------//
  
  .effect-4 {
    padding: 10px;
    display: inline-block;
    overflow: hidden;
    &:before, &:after {
      left: 0;
      width: 100%;
      height: 2px;
      background: darken($teal, 10%);
    }
    &:before {
      bottom: 0;
      transform:  translateX(-100%);
    }
    &:after {
      top: 0;
      transform:  translateX(100%);
    }
    &:hover:before, &:hover:after {
      transform:  translateX(0);
    }
  }
  
  //------- {{ effect-5 }} -------//
  
  .effect-5 {
    display: inline-block;
    overflow: hidden;
    &:before, &:after {
      right: 0;
      bottom: 0;
      background: darken(#0BAAD4, 10%);
    }
    &:before {
      width: 100%;
      height: 2px;
      transform:  translateX(-100%);
    }
    &:after {
      width: 2px;
      height: 100%;
      transform:  translateY(100%);
    }
    > span {
      display: block;
      padding: 10px;
      &:before, &:after {
        left: 0;
        top: 0;
        background: darken(#0BAAD4, 10%);
      }
      &:before {
        width: 100%;
        height: 2px;
        transform:  translateX(100%);
      }
      &:after {
        width: 2px;
        height: 100%;
        transform:  translateY(-100%);
      }
    }
    &:hover:before, &:hover:after, &:hover > span:before, &:hover > span:after {
      transform:  translate(0, 0);
    }
  }
  
  //------- {{ effect-6 }} -------//
  
  .effect-6 {
    display: inline-block;
    overflow: hidden;
    &:before, &:after {
      right: 0;
      bottom: 0;
      background: darken(#0BAAD4, 10%);
      transition: transform .3s ease;
    }
    &:before {
      width: 100%;
      height: 2px;
      transform:  translateX(-100%);
      transition-delay: .9s;
    }
    &:after {
      width: 2px;
      height: 100%;
      transform:  translateY(100%);
      transition-delay: .6s;
    }
    > span {
      display: block;
      padding: 10px;
      &:before, &:after {
        left: 0;
        top: 0;
        background: darken(#0BAAD4, 10%);
        transition: transform .3s ease;
      }
      &:before {
        width: 100%;
        height: 2px;
        transform:  translateX(100%);
        transition-delay: .3s;
      }
      &:after {
        width: 2px;
        height: 100%;
        transform:  translateY(-100%);
        transition-delay: 0s;
      }
    }
    &:hover:before, &:hover:after, &:hover > span:before, &:hover > span:after {
      transform:  translate(0, 0);
    }
  
    &:hover:before {
      transition-delay: 0s;
    }
    &:hover:after {
      transition-delay: .3s;
    }
    &:hover > span:before {
      transition-delay: .6s;
    }
    &:hover > span:after {
      transition-delay: .9s;
    }
  }
  
  //------- {{ effect-7 }} -------//
  
  .effect-7 {
    display: inline-block;
    overflow: hidden;
    &:before {
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: darken(#0BAAD4, 10%);
      transform:  translateX(-100%);
    }
    &:after {
      content: attr(data-content);
      left: 0;
      top: 0;
      width: 0;
      margin: 12px;
      color: darken(#0BAAD4, 10%);
      white-space: nowrap;
      overflow: hidden;
      transition: width .5s ease;
    }
    > span {
      display: block;
      margin: 2px;
      padding: 10px;
      background: #0BAAD4;
    }
    &:hover:before {
      transform:  translateX(0);
    }
    &:hover:after {
      width: calc(100% - 24px);
    }
  }
  
  //------- {{ effect-8 }} -------//
  
  .effect-8 {
    display: inline-block;
    overflow: hidden;
    &:before {
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: darken(#0BAAD4, 10%);
      transform:  translateY(-100%);
    }
    &:after {
      content: attr(data-content);
      top: 0;
      left: 0;
      height: 0;
      margin: 12px;
      color: darken(#0BAAD4, 10%);
      white-space: nowrap;
      overflow: hidden;
      transition: height .5s ease;
    }
    > span {
      display: block;
      margin: 2px;
      padding: 10px;
      background: #0BAAD4;
    }
    &:hover:before {
      transform:  translateY(0);
    }
    &:hover:after {
      height: calc(100% - 24px);
    }
  }