/* Pages - INICIO
–––––––––––––––––––––––––––––––––––––––––––––––––– */

.dzsparallaxer{
    color: #ffffff;
  }
  .dzsparallaxer.text-black{
    color: #222222;
  }
  
  
  .dzsparallaxer:not(.mode-oneelement){
    height: 300px;
    overflow: hidden;
    background-color: #ffffff; /*#1c1a17;*/
    position: relative;
  
    $transition-property: height;
    $transition-duration: 0.3s;
    $transition-timing: ease-in;
    @include apply_transition($transition-property, $transition-duration, $transition-timing);
  
    .dzsparallaxer--target{
      position: relative;
  
  //    $transition-property: all;
  //    $transition-duration: 0.3s;
  //    $transition-timing: ease-out;
  //    @include apply_transition($transition-property, $transition-duration, $transition-timing);
    }
    .dzsparallaxer--target.position-absolute{
      position: absolute;
      top:0; left:0;
    }
  
   
  
  }
  
  
  
  
  
  .dzsparallaxer.mode-oneelement{
    position: relative;
  }
  
  
  
  
  
  .dzsparallaxer.use-loading{
    $transition-property: opacity,visibility;
    $transition-duration: 0.7s;
    $transition-timing: ease-in;
    .dzsparallaxer--target{
      opacity: 0;
      visibility: hidden;
      @include apply_transition($transition-property, $transition-duration, $transition-timing);
    }
    $transition-property: all;
    $transition-duration: 0.3s;
    $transition-timing: ease-in;
    .preloader-semicircles {
      @include apply_transition($transition-property, $transition-duration, $transition-timing);
    }
  
    
  
    
  }
  
  
  
  
  .dzsparallaxer.height-is-based-on-content{
  
    //padding: 45px 0;
    height:auto;
    padding: 0;
  
    
    .dzsparallaxer--target{
      position: absolute;
      width: 100%;
  
    }
  
    >div{
      position: relative;
  
  
    }
  }
  .dzsparallaxer{
    >.parallaxer-content{
      pointer-events: none;
  
      >*{
        pointer-events: auto;
      }
    }
  }
  
  .dzsparallaxer:not(.height-is-based-on-content){
    >.parallaxer-content{
      position: absolute;
      top:0;
      left:0;
      width: 100%;
      height:100%;
  
    }
  }
  
  .dzsparallaxer.use-loading.loaded{
    .dzsparallaxer--target{
      opacity: 1;
      visibility: visible;
    }
    
  }
  
  .divimage{
    background-size: cover;
    background-position: center center;
    
  }
  .dzsparallaxer.is-ie-11{
    .simple-parallax-inner{
      clip: auto;
      .divimage.dzsparallaxer--target{
        background-attachment: fixed;
        position: relative;
      }
    }
  }
  

