}

.video-wrapper{
    min-height: 80vh;

    .vid{
        width: 100px;
        height: 100px;
        line-height: 100px;
        text-align: center;
        border: 1px solid #fff;
        border-radius: 50%;
        position: relative;
        z-index: 5;
    }
}

.nex-prv{
    display: flex;
    
    .nxt,
    .prv{
        width: 50%;
        padding: 0 2px;

        .img{
            padding: 30px;
            background-size: cover;
            background-position: center center;
            position: relative;
            transition: all .4s;

            &:hover{
                color: #fff;

                &:after{
                    opacity: 0;
                }
            }

            &:after{
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: #eee;
                transition: all .4s;
            }

            .overly{
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                opacity: .5;
            }

            .text-center{
                position: relative;
                z-index: 4;
            }
        }
    }
}

