/*Portfolio page*/
.portfolio-content {
	position: relative;

	img {
		width: 100%;
	}
}

.portfolio-attributes {
	color: $body-color;
	font-size: 12px;
	overflow: hidden;
	text-transform: uppercase;
	margin-bottom: 20px;

	a:not(.btn) {
		color: $body-color;
	}

	.attribute {
		float: left;
		margin-bottom: 20px;
		padding-right: 20px;
		text-align: left;
		width: auto;

		strong {
			opacity: 0.6;
			border-bottom: 1px solid rgba(31, 31, 31, 0.05);
			display: block;
			width: auto;
			margin-bottom: 10px;
			font-size: 11px;
		}
	}

	&.style1 {
		.attribute {
			display: inline-flex;
			float: none;
			margin-bottom: 6px;

			strong {
				border: 0;
				margin-right: 8px;
				width: auto;
			}
		}
	}

	&.style2 {
		.attribute {
			float: none;
			width: 100%;

			strong {
				border: 0;
				width: 100%;
			}
		}
	}
}

.project-description {
	margin-bottom: 40px;

	h2 {
		font-size: 32px;
		font-weight: 600;
		margin-bottom: 20px;
	}

	h3 {
		font-size: 18px;
		font-weight: 400;
        line-height: 2em;
		margin-bottom: 20px;
	}
}

/*Portfolio Ajax  Page*/
.portfolio-ajax-page {
	[class*="col-"] {
		padding: 0;
	}

	.project-description {
		padding: 24px 40px;
		margin-bottom: 0;

		>p {
			padding-bottom: 6px;
		}

		.portfolio-attributes {
			margin-top: 20px;

			.attribute {
				margin-bottom: 0;
				width: 100%;

				>strong {
					min-width: 68px;
				}
			}
		}
	}
}
