/* generic site wide styles*/
body {
    overflow-x: hidden;
    background-color: $gray-100;
}

/* disable pointer events */
.disable-child-pointer * {
    pointer-events: none;
}

/* text link cover full area. Used mostly with cards */
.link-cover {
    &:after {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 30;
      pointer-events: auto;
      content: "";
    }
}

.btn:focus, button:focus, input:focus, .navbar-toggler:focus {
    box-shadow: none;
}