	.info-area{
		border-bottom: 1px solid #eee;

		.tags,
		.share-icon{

			span{
				font-size: 14px;
				margin-right: 5px;
				opacity: .7;
			}

             a{
				padding: 7px 20px;
				font-size: 13px;
				background: #f7f7fe;
				border-radius: 30px;
				margin-right: 5px;

				
			}

			
		}


        .tags{

            a{
				padding: 7px 20px;
				font-size: 13px;
				background: $yellow;
				border-radius: 5px;
				margin-right: 5px;

				&:hover{
					background: $primary;
					color: #fff;
				}
			}

        }
		.share-icon{

			a{
				width: 35px;
				height: 35px;
				line-height: 35px;
				font-size: 11px;
				text-align: center;
				padding: 0;
			}
		}
	}

	.author-area{

		.author-img{

			.img{
				width: 150px;
				height: 150px;
				border-radius: 50%;
				overflow: hidden;
			}
		}
	}
	

	
}

.post-alt{
	.next-prv-post{

		.thumb-post{
			width: 100%;
			padding: 40px;
			position: relative;
			overflow: hidden;
			z-index: 2;

			&:after{
				content: '';
				background: $white;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				border: 1px solid $gray-300;
				z-index: -1;
				transition: all .4s;
			}

			&:before{
				content: '';
				background: $primary;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				z-index: -1;
				opacity: 0;
				transition: all .4s;
			}

			&:hover{
				
				h6{
					color: $gray-100;
				}

				&:before{
					opacity: .5;
				}

				&:after{
					opacity: 0;
				}
			}

			&:first-of-type{
				margin-right: 15px;
			}

			&:last-of-type{
				margin-left: 15px;
			}
		}
	}
}

.comments-post{

	.item-box{
		padding: 30px 0;

		&.replayed{
			margin-left: 50px;

			.user-img{

				.img{
					width: 70px;
					height: 70px;
					border-radius: 50%;
					overflow: hidden;
				}
			}
		}

		&.next-step{
			margin-left: 50px;
			background: $white;
			padding: 8px;
			border-radius: 10px;
		}

		.replay-post{

			a{
				font-size: 13px;
				padding: 8px 5px;
				

				
			}
		}


		
	}
}

.comments-from{

	form{

		input,
		textarea{
			padding: 15px;
			width: 100%;
			background: transparent;
			border: 1px solid $secondary;
			border-radius: 5px;
		}

		button{
			border: 0;
			margin: auto;
			text-align: center;
		}
	}
}

.side-bar{

	.search-box{
		position: relative;

		input{
			padding: 15px;
			border: 1px solid $secondary;
			border-radius: 5px;
			width: 100%;
			background: transparent;
		}

		.icon{
			position: absolute;
			top: 50%;
			right: 15px;
			transform: translateY(-50%);
		}
	}

	.widget{
		padding: 30px 30px;
		margin-top: 25px;
		border-radius: 10px;
		border: 1px solid #eee;

		.title-widget{
			margin-bottom: 25px;
		}
		
		&.catogry{

			li{
				display: flex;
				font-size: 14px;
				margin-bottom: 15px;

				&:last-of-type{
					margin-bottom: 0;
				}
			}
		}

		&.last-post-thum{

			.item{
				display: flex;
				margin-bottom: 30px;

				&:last-of-type{
					margin-bottom: 0;
				}

				.img{
					width: 70px;
					height: 70px;
					border-radius: 5px;
					overflow: hidden;

					a{
						width: 100%;
						height: 100%;
					}

					img{
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}

				.cont{
					padding-left: 15px;

					h6{
						font-size: 16px;
					}

					span{
						font-size: 13px;
						opacity: .8;
						margin-top: 10px;
					}
				}
			}
		}

		&.tags{

			a{
				padding: 4px 20px;
				border-radius: 5px;
				background: $yellow;
				font-size: 13px;
				margin: 2px 0;
			}
		}

		&.gallry{

			li{
				margin: 5px;
			}
		}
	}
}

