.container {
    padding-left: 1rem;
    padding-right: 1rem;
  }
  
  
  
  
  .highlights2 {
  background-color: #030314;
    margin-bottom: 200px;
    overflow: hidden;
    padding-bottom: 185px;
    position: relative;
  }
  
  
  .highlights2 .watermark {
  bottom: -124px;
  height: 348px;
  left: 57%;
  overflow: hidden;
  position: absolute;
  text-align: center;
  transform: translateX(-50%);
  width: 100%;
  z-index: 0;
  }
  
  .watermark.white .fill {
  fill: rgba(255, 255, 255, 0.303);
  }
  
  .valign {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }
  
  .flex {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  
  }
  
  .circle-img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    -o-object-position: center;
    object-position: center;
  }
  
  .circle-70 {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    text-align: center;
    line-height: 80px;
    overflow: hidden;
  }
  
  .ml-auto {
    margin-left: auto;
  }
  
  .sticky-sidebar{
    position: -webkit-sticky;
    position: sticky;
    top: 95px;
  }
  
  .radius-5{
      border-radius: 5px;
    }
    
    .radius-10{
      border-radius: 10px;
    }
    
    .radius-15{
      border-radius: 15px;
    }
    
    .radius-20{
      border-radius: 20px;
    }
    
    .radius-25{
      border-radius: 25px;
    }
    
    .radius-30{
      border-radius: 30px;
    }
  
  .circle {
      border-radius: 50%;
    }
  
    .no-bord{
      border: 0 !important;
    }
  
  
  
    .primery-shadow {
      box-shadow: 0px 8px 30px -4px rgba(0, 0, 0, .6);
    }
  
    
  
  
    .unlist {
      list-style: none;
    }
  
  
  
  .no-padding {
      padding: 0 !important;
    }
  
  
    .no-padding-top {
      padding-top: 0 !important;
    }
  
  
    .no-padding-bottom {
      padding-bottom: 0 !important;
    }
  
  
    .full-width {
      width: 100% !important;
    }
    
    .bg-img {
      background-size: cover;
      background-repeat: no-repeat;
    }
  
    .bg-img-hero {
      background-size: cover;
      background-repeat: no-repeat;
      background-position: top center;
  }

  
    @media (max-width: 991.98px){
      .min-height-md-50hv {
        padding-top: 250px !important;
        padding-bottom: 250px !important;
        background-position: center;
    }
    }
   
  
    
  
    @media (min-width: 992px) {
  
      .hero-home, 
      .hero-servicios{
        height: 100vh;
        width: 100vw;
  
      } 
  
  
    .fullscreen{
      height: 100vh;
      width: 100vw;
    }
    
    .halfscreen{
        height: 50vh;
        width: 100vw;
      } 
  
    .quarterscreen{
      height: 25vh;
      width: 100vw;
    }
  
    
      .px-lg-0 {
          padding-right: 0 !important;
      }
  
      .px-lg-0 {
          padding-left: 0 !important;
      }
      .top-lg-0 {
          top: 0;
      }
  
      .right-lg-0 {
          right: 0;
      }
  
      .position-lg-absolute {
          position: absolute;
      }
      .min-height-lg-100vh {
          min-height: 100vh;
      }
      .w-lg-40{
        width: 40% !important;
      }
  }  