.nav-link {
    margin-left: 1.5rem;
    margin-right: 1.5rem;
}

/* on body scroll transition */
/* we use a CSS class added by JS after page load to avoid content shifting during page load */
.page-loaded .navbar.fixed-top {
    transition: padding .2s cubic-bezier(.42,0,.58,1);
}

.is-scrolling .navbar.fixed-top {
    padding-top: .65rem;
    padding-bottom: .65rem;
    box-shadow: $box-shadow-lg;

    .btn {
        padding-top: .75rem;
        padding-bottom: .75rem;
    }
}

.middle-bar {
    margin-top: 0px;
}

.navbar-toggler {
    width: 20px;
    height: 20px;
    position: relative;
    transition: .5s ease-in-out;
    outline: none;
    box-shadow: none;
    border: 0;
    position: relative;
    &:focus {
        outline: none;
        box-shadow: none;
        border: 0;
        position: relative;
    }
    &:active {
        outline: none;
        box-shadow: none;
        border: 0;
        position: relative;
    }
    span {
        margin: 0;
        padding: 0;
    }
    .top-bar {
        top: inherit;
        transform: rotate(135deg);
    }
    .middle-bar {
        opacity: 0;
        top: inherit;
        filter: alpha(opacity=0);
    }
    .bottom-bar {
        top: inherit;
        transform: rotate(-135deg);
    }
}
.navbar-toggler-icon {
    &:focus {
        outline: none;
        box-shadow: none;
        border: 0;
        position: relative;
    }
}
.toggler-icon {
    display: block;
    position: absolute;
    height: 3px;
    width: 100%;
    background: $secondary;
    border-radius: 1px;
    opacity: 1;
    left: -20px;
    transform: rotate(0deg);
    transition: .25s ease-in-out;
}

.toggler-icon-light {
    display: block;
    position: absolute;
    height: 3px;
    width: 100%;
    background: $white;
    border-radius: 1px;
    opacity: 1;
    left: -20px;
    transform: rotate(0deg);
    transition: .25s ease-in-out;
}

.navbar-toggler.collapsed {
    .top-bar {
        position: absolute;
        top: 0px;
        transform: rotate(0deg);
    }
    .middle-bar {
        opacity: 1;
        position: absolute;
        top: 10px;
        filter: alpha(opacity=100);
    }
    .bottom-bar {
        position: absolute;
        top: 20px;
        transform: rotate(0deg);
    }
    .toggler-icon {
        background: $primary;
    }

}