/*DIVIDER*/

.divider {
    display: -ms-flexbox;
    display: flex;
    background: $indigo;
  }

  .divider--primary {
    display: -ms-flexbox;
    display: flex;
    background: #030314;
  }

.divider__left, .divider__right {
    position: relative;
    display: block;
    background: #030314;
    height: 70px;
    width: 50%;
  }

  .divider__left2, .divider__right_2 {
    position: relative;
    display: block;
    background: $indigo;
    height: 70px;
    width: 50%;
  }
  
  .divider__left,
  .divider__left_2 {
    border-top-right-radius: 70px;
  }
  
  .divider__right,
  .divider__right_2 {
    border-top-left-radius: 70px;
  }
  
  .divider--indigo {
  background: $light;
  padding-top: 20px;
  }

  .divider--indigo .divider__left, .divider--purple .divider__right {
    background: $indigo;
  }
/*FIN - DIVIDER*/



.wave-divider-top {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
}

.wave-divider-top svg {
  position: relative;
  display: block;
  width: calc(150% + 1.3px);
  height: 83px;
  transform: rotateY(180deg);
}

.wave-divider-top .shape-fill {
  fill: $gray-100;
}

/** For mobile devices **/
@media (max-width: 767px) {
  .wave-divider-top svg {
      width: calc(245% + 1.3px);
      height: 83px;
  }
}